import React, { Component } from 'react';
import './App.css';
//import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './components/layouts/Header';
import Navbar from './components/layouts/Navbar';
import FooterSection from './components/layouts/FooterSection';
import Footer from './components/layouts/Footer';
//React Router Import
import { Switch, Route } from 'react-router-dom';

//Import Pages
import Home from './components/pages/Home';
import LocalMovers from './components/pages/LocalMovers';
import LongDistanceMovers from './components/pages/LongDistanceMovers';
import CommercialMovers from './components/pages/CommercialMovers';
import VehicleMovers from './components/pages/VehicleMovers';
import OfficeRelocation from './components/pages/OfficeRelocation';
import HomeShifting from './components/pages/HomeShifting';
import About from './components/pages/About';
import PackersAndMovers from './components/pages/PackersAndMovers';
import Contact from './components/pages/Contact';
import NotFound from './components/pages/NotFound';

class App extends Component {
  render() {
    return (
      <div>
        <Header />
        <Navbar />
        <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/localmovers' component={LocalMovers} />
          <Route path='/longdistancemovers' component={LongDistanceMovers} />
          <Route path='/commercialmovers' component={CommercialMovers} />
          <Route path='/vehiclemovers' component={VehicleMovers} />
          <Route path='/officerelocation' component={OfficeRelocation} />
          <Route path='/homeshifting' component={HomeShifting} />
          <Route path='/about' component={About} />
          <Route path='/packersandmovers' component={PackersAndMovers} />
          <Route path='/contact' component={Contact} />
          <Route component={NotFound} />
        </Switch>
        <FooterSection />
        <div className='clearfix'></div>
        <Footer />
      </div>
    );
  }
}

export default App;
